.header-nav {
  position: sticky;
  top: 0;
  z-index: 20;
  background: linear-gradient(90deg, #fdf7f4 0%, #f5f5ff 50%, #f3f8ff 100%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
}

.header-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 72px;
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-family: var(--font-serif);
}

.logo-image {
  height: 40px;
  width: auto;
  object-fit: contain;
}

.logo-text {
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #475569;
}

.header-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  font-size: 0.95rem;
  color: var(--color-text-light);
}

.header-links a,
.header-links button {
  position: relative;
  padding-bottom: 4px;
  background: none;
  border: none;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

.header-admin-link {
  font-weight: 500;
  color: #1d4ed8;
}

.header-admin-link::after {
  display: none;
}

.header-coming-soon {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

.header-coming-soon::after {
  display: none;
}

.header-links a::after,
.header-links button::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: var(--color-accent);
  transition: width 0.2s ease;
}

.header-links a:hover::after,
.header-links a.active::after {
  width: 100%;
}

.header-links a.active {
  color: var(--color-text);
  font-weight: 500;
}

/* Dropdown navigation */
.nav-dropdown {
  position: relative;
}

.nav-dropdown-trigger {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
}

.nav-dropdown-trigger .nav-dropdown-caret {
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid currentColor;
  margin-top: 2px;
}

.nav-dropdown-menu {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  min-width: 220px;
  padding: 0.4rem 0;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.12);
  opacity: 0;
  pointer-events: none;
  transform: translateY(8px);
  transition: opacity 0.18s ease, transform 0.18s ease;
  z-index: 9999;
}

.nav-dropdown-menu::before {
  content: '';
  position: absolute;
  top: -8px;
  left: 18px;
  width: 14px;
  height: 14px;
  background: #ffffff;
  transform: rotate(45deg);
  box-shadow: -4px -4px 10px rgba(15, 23, 42, 0.04);
  z-index: -1;
}

.nav-dropdown.open .nav-dropdown-menu {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0);
}

.nav-dropdown-link {
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  white-space: nowrap;
  color: #4b5563;
  pointer-events: auto;
}

.nav-dropdown-link:hover {
  background: #f3f4ff;
  color: #111827;
}

.nav-dropdown-menu-disabled,
.nav-dropdown-link-disabled,
.nav-dropdown-trigger-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Nested Dropdown */
.nav-dropdown-nested {
  position: relative;
}

.nav-dropdown-nested-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nav-dropdown-menu-nested {
  position: absolute;
  top: 0;
  left: 100%;
  min-width: 220px;
  padding: 0.4rem 0;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.12);
  opacity: 0;
  pointer-events: none;
  transform: translateX(-8px);
  transition: opacity 0.18s ease, transform 0.18s ease;
  z-index: 10000;
  margin-left: 8px;
}

.nav-dropdown-nested.open .nav-dropdown-menu-nested {
  opacity: 1;
  pointer-events: auto;
  transform: translateX(0);
}

.nav-dropdown-menu-nested::before {
  content: '';
  position: absolute;
  top: 18px;
  left: -8px;
  width: 14px;
  height: 14px;
  background: #ffffff;
  transform: rotate(45deg);
  box-shadow: -4px -4px 10px rgba(15, 23, 42, 0.04);
  z-index: -1;
}

/* CTA button */
.header-cta-button {
  margin-left: auto;
  padding: 0.55rem 1.8rem;
  border-radius: 999px;
  border: none;
  /* Match Pamela Shore gradient style */
  background: linear-gradient(135deg, #1e40af 0%, #2563eb 50%, #3b82f6 100%) !important;
  color: #ffffff;
  font-size: 0.95rem;
  letter-spacing: 0.04em;
  text-transform: none;
  font-weight: 500;
  box-shadow: 0 14px 35px rgba(37, 99, 235, 0.35);
  opacity: 1 !important;
}

.header-cta-button:link,
.header-cta-button:visited,
.header-cta-button.active {
  color: #ffffff;
}

.header-cta-button::after {
  display: none;
}

/* Mobile Menu Toggle Button */
.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 28px;
  height: 28px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 21;
  position: relative;
}

.hamburger-line {
  width: 100%;
  height: 2.5px;
  background-color: #1f2937;
  border-radius: 2px;
  transition: all 0.3s ease;
  transform-origin: center;
}

.mobile-menu-toggle .hamburger-line.open:nth-child(1) {
  transform: rotate(45deg) translate(7px, 7px);
}

.mobile-menu-toggle .hamburger-line.open:nth-child(2) {
  opacity: 0;
  transform: translateX(-10px);
}

.mobile-menu-toggle .hamburger-line.open:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

@media (max-width: 768px) {
  .header-inner {
    height: 64px;
  }

  .mobile-menu-toggle {
    display: flex;
  }

  .header-links {
    position: fixed;
    top: 64px;
    left: 0;
    right: 0;
    background: linear-gradient(180deg, #fdf7f4 0%, #f5f5ff 50%, #f3f8ff 100%);
    backdrop-filter: blur(12px);
    flex-direction: column;
    align-items: flex-start;
    padding: 2rem 1.5rem;
    gap: 1.5rem;
    box-shadow: 0 10px 30px rgba(15, 23, 42, 0.1);
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
    z-index: 19;
    max-height: calc(100vh - 64px);
    overflow-y: auto;
  }

  .header-links.mobile-menu-open {
    transform: translateX(0);
  }

  .header-links a,
  .header-links button {
    width: 100%;
    padding: 0.75rem 0;
    font-size: 1.1rem;
    border-bottom: 1px solid rgba(229, 231, 235, 0.5);
    text-align: left;
  }

  .header-links a:last-child,
  .header-links button:last-child {
    border-bottom: none;
  }

  .header-links a::after,
  .header-links button::after {
    display: none;
  }

  .header-links a:hover,
  .header-links a.active {
    color: #1d4ed8;
    font-weight: 600;
  }

  .nav-dropdown-menu {
    position: static;
    opacity: 0;
    pointer-events: none;
    transform: none;
    box-shadow: none;
    background: transparent;
    padding: 0.25rem 0 0.5rem;
  }

  .nav-dropdown.open .nav-dropdown-menu {
    opacity: 1;
    pointer-events: auto;
  }

  .nav-dropdown-menu::before {
    display: none;
  }

  .nav-dropdown-link {
    padding: 0.35rem 0;
    border-radius: 0;
  }

  .nav-dropdown-menu-nested {
    position: static;
    opacity: 0;
    pointer-events: none;
    transform: none;
    box-shadow: none;
    background: transparent;
    padding: 0.25rem 0 0.5rem;
    margin-left: 0;
  }

  .nav-dropdown-nested.open .nav-dropdown-menu-nested {
    opacity: 1;
    pointer-events: auto;
  }

  .nav-dropdown-menu-nested::before {
    display: none;
  }

  .header-cta-button {
    margin-left: 0;
    width: 100%;
    text-align: center;
    padding: 0.9rem 0;
    border-radius: 999px;
    border: none;
  }
}